import React from 'react';
import { RmdbMovie, RmdbMovieGrid } from './Movie.style';
import { API_URL, API_KEY } from '../../config';
import Navigation from '../Navigation/Navigation';
import MovieInfo from '../MovieInfo/MovieInfo';
import MovieInfoBar from '../MovieInfoBar/MovieInfoBar';
import FourColGrid from '../FourColGrid/FourColGrid';
import Actor from '../Actor/Actor';
import Spinner from '../Spinner/Spinner';

class Movie extends React.Component {
  state = {
    movie: null,
    actors: null,
    directors: [],
    loading: false,
  };

  componentDidMount() {
    const { movieId } = this.props.match.params;
    if (localStorage.getItem(`${movieId}`)) {
      let state = JSON.parse(localStorage.getItem(`${movieId}`));
      this.setState({ ...state });
    } else {
      this.setState({
        loading: true,
      });
      let url = `${API_URL}movie/${movieId}?api_key=${API_KEY}&language=en-US`;
      this.fetchItems(url);
    }
  }

  fetchItems = async (url) => {
    const { movieId } = this.props.match.params;
    const res = await fetch(url);
    const data = await res.json();
    console.log(data);
    if (data.status_code) {
      this.setState({
        loading: false,
      });
    } else {
      this.setState(
        {
          movie: data,
        },
        () => {
          let url = `${API_URL}movie/${movieId}/credits?api_key=${API_KEY}`;
          fetch(url)
            .then((res) => res.json())
            .then((data) => {
              const directors = data.crew.filter(
                (member) => member.job === 'Director'
              );
              this.setState(
                {
                  actors: data.cast,
                  directors,
                  loading: false,
                },
                () => {
                  localStorage.setItem(
                    `${movieId}`,
                    JSON.stringify(this.state)
                  );
                }
              );
            });
        }
      );
    }
  };
  render() {
    const { movieName } = this.props.location;
    const { movie, directors, actors, loading } = this.state;
    return (
      <>
        <RmdbMovie>
          {movie ? (
            <div>
              <Navigation movie={movieName} />
              <MovieInfo movie={movie} directors={directors} />
              <MovieInfoBar
                time={movie.runtime}
                budget={movie.budget}
                revenue={movie.revenue}
              />
            </div>
          ) : null}
          {actors ? (
            <RmdbMovieGrid>
              <FourColGrid header={'Actors'}>
                {actors.map((element, i) => (
                  <Actor key={i} actor={element} />
                ))}
              </FourColGrid>
            </RmdbMovieGrid>
          ) : null}
          {!actors && !loading ? <h1>No movie found</h1> : null}
          {loading ? <Spinner /> : null}
        </RmdbMovie>
      </>
    );
  }
}

export default Movie;
